.x-nav{
  position: sticky;
  top: 0;
  z-index: 3;
  height: 82px;
}
nav {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  padding: .5rem 1.5rem;
  width: 100%;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}
.x-flash-sale{
  font-size: .8em;
  text-align: center;
  background: var(--primary-color);
  color: #fff;
  height: 23px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.logo-image {
  width: 55px;
  margin: 5px 0;
 
}

nav .x-country{
  display: none;
}

.nav-logo-home-button {
  align-self: center;
  cursor: pointer;
}

.nav-logo-home-button a {
  outline: none;
  border: none;
  /* color: rgb(175, 175, 175); */
  color: #111;
  text-decoration: none;
  font-weight: 400;
  display: flex;
  align-items: center;
}

.nav-logo-home-button a:hover {
  color: #111;
}

a:active {
  outline: none;
  border: none;
}
a:focus {
  outline: none;
  border: none;
}
.x-right-container {
  display: flex;
  justify-content: center;
  align-items: center;

}
.x-right-container > * {
  padding: 0 1em;
  text-decoration: unset;
}
.x-right-container  .hamburger-icon.border{
  border: 1px solid #ddd;
  padding: .5em .8em;
  border-radius: 5px;
  margin: auto;
}
.hamburger-icon {
  align-self: center;
  cursor: pointer;
  color: rgb(175, 175, 175);
}

.hamburger-icon:hover {
  /* color: white; */
}

.nav-link-container {
  display: none;
}

input:focus {
  outline: none;
}

.nav-link-container > * {
  margin-right: 2.3em;
  position: relative;
}
.nav-link-container > *:hover {
  /* background-color: rgb(42, 42, 42); */
  /* border: 1px solid rgb(42, 42, 42); */
}

.nav-link-container-mobile {
  padding: 1em;
  padding-top: 70px;
  border-radius: 10px;
  position: absolute;
  display: flex;
  flex-flow: column nowrap;
  top: 0px;
  width: 100vw;
  right: 0;
  /* height: 80vh; */
  justify-content: flex-start;
  background-color: rgb(255, 255, 255);
  align-items: stretch;
  z-index: 1;
  /* background-color: rgb(0, 0, 0); */
  box-shadow: 0px 0px 46px -4px rgb(176, 175, 175);
}
.nav-link-container-mobile > * {
  color:rgb(108, 108, 108);
  /* margin-bottom: 10px; */
  /* border: 1px solid rgb(24, 24, 24); */
  margin-right: 0px;
  /* text-align: center; */
  text-decoration: none;
  padding: 20px 30px;
  border-radius: 10px;
  font-weight: 400;
  font-size: 1rem;
  letter-spacing: 1px;
  display: flex;
  justify-content: space-between;
  /* background-color: rgb(24, 24, 24); */
}
.nav-link-container-mobile 
.cross-tab-icon {
  position: relative;
  top: 8px;
  left: 2px;
  cursor: pointer;
}

::placeholder {
  letter-spacing: 0.4px;
}

.cross-tab-icon {
  z-index: 1;
  color: red;
  padding: 1em;
}

.nav-input-search {
  margin-top: 0.4em;
  border: none;
  background-color: #f7f7f7;
  border-radius: 18px;
  display: flex;
  align-items: center;
  height: 30px;
}

.nav-input-search input {
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 15px;
  background-color: #f7f7f7;
}

.nav-input-search button {
  border: none;
  background-color: #f7f7f7;
  padding: 0.5rem 1rem;
  border-radius: 15px;
  margin-top: 0.1em;
  padding-bottom: 5px;
  cursor: pointer;
}

.cart-count {
  font-size: .7rem;
  position: relative;
  display: inline-block;
  padding: 2.5px 6px;
  border-radius: 30px;
  left: -5px;
  bottom: 15px;
  background-color: rgb(250, 103, 103);
  color: white;
  border: none;
}
.cart,
.wishlist {
  position: relative;
  top: 5px;
  left: 10px;
}
.nav-logo-home-button .brand-name {
  /* display: none; */
  font-weight: 600;
}
@media (max-width: 767px) {
  nav{
    padding: .5em 1em
  }
  .x-right-container{
    width: 70px;
  }
  .nav-input-search{
    height: 40px;
    margin: auto;
  }
}
@media (min-width: 768px) {
  nav a {
    display: inline-block;
    /* color: white; */
    font-size: 1.2em;
    font-weight: 300;
    text-decoration: none;
    letter-spacing: 0.2px;
  }
  .nav-logo-home-button .brand-name {
    display: inline;
  }
  nav .x-country{
    display: block;
  }
  nav {
    z-index: 3;
    align-items: center;
    position: sticky;
    top: 0;
  }
  .x-right-container {
    display: none; 
  }
  .hamburger-icon {
    display: none;
  }
  .nav-link-container {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
  }

  .nav-link-container > * {
    /* color: rgb(176, 175, 175); */
  color: #111;
  font-size: 1em;

  }
  .nav-link-container > *:hover {
    /* background-color: black; */
    cursor: pointer;
    /* color: white; */
    border: none;
  }

  .cart-count {
    position: absolute;
    display: block;
    padding: 2.5px 6px;
    border-radius: 30px;
    left: 15px;
    bottom: 20px;
    background-color: rgb(253, 87, 87);
    color: white;
    border: none;
    /* border: 1px solid var(--primary-color); */
  }

  .cart a {
    color: white;
  }

  .cart,
  .wishlist {
    position: default;
    top: 0;
    left: 0;
    margin-top: 0.2em;
  }
  .wishlist {
    margin-top: 0.2em;
  }
  .logo-image{
    margin-left: 40px;
  }
}
